<template>
<div class="container container-sm">
    <div class="breadcrumb">
        <router-link to="/profile">C Nhn</router-link>
        <router-link to="/profile/edit">Sa Thng Tin C Nhn</router-link>
    </div>

    <form action="" @submit.prevent="updateAvatar()">
        <img :src="avatarPreview" :alt="user.username" class="d-block mx-auto avatar-xl" />
        <br>

        <p>Cp nht nh i din</p>

        <label class="input">
            <input type="file" @change="onchangeAvatar">
        </label>

        <button class="btn btn-primary">
            <span class="material-icons mr-2">file_upload</span>
            Upload
        </button>
    </form>
    <hr>

    <form action="" @submit.prevent="startUpdateProfile()">
        <p>Cp nht thng tin c nhn</p>

        <label class="input">
            <span class="material-icons">label</span>
            <input type="text" v-model="form.name" placeholder="Tn hin th" />
            <p>Tn hin th</p>
        </label>

        <label class="d-flex align-items-center">
            <span class="material-icons mr-2 ml-2" style="color: #ff907f">palette</span>
            <input type="color" v-model="form.color" />
            <small class="ml-3 text-secondary">Mu hin th</small>
        </label>

        <label class="input">
            <span class="material-icons">favorite</span>
            <input type="text" v-model="form.slogan" placeholder="Chm ngn" />
            <p>Chm ngn</p>
        </label>

        <button class="btn btn-primary">
            <span class="material-icons mr-2">save</span>
            Save
        </button>
    </form>
    <hr>

    <form action="" @submit.prevent="startUpdatePassword()">
        <p>Cp nht mt khu</p>

        <label class="input">
            <span class="material-icons">lock</span>
            <input type="password" v-model="passwordForm.oldPassword" placeholder="Mt khu c" />
            <p>Mt khu c</p>
        </label>

        <label class="input">
            <span class="material-icons">lock</span>
            <input type="password" v-model="passwordForm.newPassword" placeholder="Mt khu mi" />
            <p>Mt khu mi</p>
        </label>

        <label class="input">
            <span class="material-icons">lock</span>
            <input type="password" v-model="rePassword" placeholder="Xc nhn mt khu" />
            <p>Xc nhn mt khu</p>
        </label>

        <button class="btn btn-primary">
            <span class="material-icons mr-2">save</span>
            Save
        </button>
    </form>

    <message-popup
    :title="message.title"
    :message="message.message"
    :type="message.type"
    :is-display="message.isDisplay" />
</div>
</template>

<script>
import { mapState, mapMutations, mapActions, mapGetters } from 'vuex'
import api from '@/store/api'
import MessagePopup from '@/components/MessagePopup.vue'

export default {
    name: 'EditProfileView',

    components: {
        MessagePopup
    },

    data () {
        return {
            form: {
                name: this.getUserName(),
                slogan: this.getUserSlogan(),
                color: this.getUserColor()
            },

            passwordForm: {
                oldPassword: '',
                newPassword: ''
            },

            rePassword: '',

            avatarPreview: api.getAvatarURL(this.getUserUsername()),
            avatarFile: null,

            isLoading: false,

            message: {
                title: '',
                message: '',
                type: '',
                isDisplay: false
            }
        }
    },

    computed: {
        ...mapState(['user']),
    },

    methods : {
        ...mapMutations(['setIsLoading']),
        ...mapActions(['updateProfile', 'updatePassword']),
        ...mapGetters(['getUserName', 'getUserSlogan', 'getUserUsername', 'getUserColor']),

        updateAvatar () {
            if (!this.avatarFile) {
                this.showMessage(
                    'Li',
                    'Vui lng chn nh i din',
                    'error'
                )

                return
            }

            let data = new FormData()
            data.append('avatar', this.avatarFile)

            this.setIsLoading(true)

            api.updateAvatar(data)
            .then(res => {
                if (res.status == 200) {
                    location.reload()
                }
                else {
                    this.setIsLoading(false)

                    this.showMessage(
                        'Li!',
                        'C li xy ra, vui lng th li',
                        'error'
                    )
                }
            })
            .catch(err => {
                this.setIsLoading(false)

                console.log(err)

                this.showMessage(
                    'Li!',
                    'Khng th kt ni n my ch',
                    'error'
                )
            })
        },

        async startUpdateProfile () {
            if (!this.form.name || !this.form.slogan) {
                this.showMessage(
                    'Li',
                    'Vui lng nhp y  thng tin',
                    'warning'
                )

                return
            }

            let isSuccess;

            try {
                isSuccess = await this.updateProfile(this.form)
            }
            catch (err) {
                console.log(err.message)

                let msg = ''

                if (err.message == 'Invalid name') msg = 'Tn hin th phi ln hn 2 v nh hn 50 k t'
                else msg = 'C li xy ra, vui lng th li'

                this.showMessage(
                    'Li!',
                    msg,
                    'error'
                )
            }

            if (!isSuccess) return

            this.showMessage(
                'Thnh cng',
                'Cp nht thng tin thnh cng',
                'success'
            )
        },

        async startUpdatePassword () {
            if (!this.passwordForm.oldPassword || !this.passwordForm.newPassword) {
                this.showMessage(
                    'Li',
                    'Vui lng nhp y  thng tin',
                    'warning'
                )

                return
            }

            if (this.passwordForm.newPassword != this.rePassword) {
                this.showMessage(
                    'Li',
                    'Mt khu mi v xc nhn mt khu khng khp',
                    'warning'
                )

                return
            }

            try {
                if (!(await this.updatePassword(this.passwordForm))) return

                this.showMessage(
                    'Thnh cng',
                    'Cp nht mt khu thnh cng',
                    'success'
                )

                this.$router.push('/profile')
            }
            catch (err) {
                console.log(err.message)

                let msg = ''

                if (err.message == 'Invalid password') msg = 'Mt khu phi t 6 k t, bao gm ch in hoa, ch in thng, s v k t c bit'
                else if (err.message == 'Old password is incorrect') msg = 'Mt khu c khng chnh xc'
                else msg = 'C li xy ra, vui lng th li'

                this.showMessage(
                    'Li!',
                    msg,
                    'error'
                )
            }
        },

        onchangeAvatar (e) {
            let files = e.target.files || e.dataTransfer.files

            if (!files.length) return

            this.convertAvatar(files[0])
        },

        // Convert avatar to 256x256 png file
        convertAvatar (avatar) {
            let canvas = document.createElement('canvas')
            let ctx = canvas.getContext('2d')
            let img = document.createElement('img')
            let scale = 256
            
            canvas.width = scale
            canvas.height = scale
            
            img.src = URL.createObjectURL(avatar)

            img.onload = () => {
                URL.revokeObjectURL(img.src)
                
                if (img.width > img.height) {
                    let s = img.height / scale
                    let w = img.width / s
                    
                    ctx.drawImage(img, 0, 0, w, scale)
                }
                else {
                    let s = img.width / scale
                    let h = img.height / s
                    
                    ctx.drawImage(img, 0, 0, scale, h)
                }
                
                this.avatarPreview = canvas.toDataURL()

                fetch(this.avatarPreview).then(res => {
                    res.arrayBuffer().then(buf => {
                        this.avatarFile = new File([buf], 'avatar', {type: 'image/png'})
                    })
                })
            }
        },

        showMessage (title, message, type) {
            this.message.title = title
            this.message.message = message
            this.message.type = type
            this.message.isDisplay = !this.message.isDisplay
        }
    }
}
</script>