<template>
<div>
    <Transition name="fade-in">
        <div class="popup" v-show="display">
            <div class="popup-bg" @click="display = false"></div>

            <div class="container container-sm">
                <div class="success">
                    <div class="d-flex justify-content-center align-items-center">
                        <span class="material-icons mr-2">notifications_active</span>
                        <h3>Bt Thng Bo</h3>
                    </div>

                    <span class="material-icons btn-close" @click="display = false">close</span>
                </div>

                <div>
                    <p>Bn c mun nhn thng bo khi c hot ng mi khng?</p>

                    <div>
                        <button @click="requestPermission" class="btn btn-success d-inline-block">C</button>
                        <button @click="display = false" class="btn d-inline-block ml-2">Khng</button>
                    </div>
                </div>
            </div>
        </div>
    </Transition>

    <!-- Message -->
    <MessagePopup
    :title="message.title"
    :message="message.message"
    :type="message.type"
    :is-display="message.isDisplay" />
</div>
</template>

<script>
import MessagePopup from '@/components/MessagePopup.vue'

export default {
    name: 'RequestNotifi',

    components: {
        MessagePopup
    },

    created () {
        this.showRequestPermissionPopup()
    },

    data () {
        return {
            display: false,

            message: {
                title: '',
                message: '',
                type: '',
                isDisplay: false
            }
        }
    },

    methods: {
        showRequestPermissionPopup () {
            if (Notification.permission === "default") {
                setTimeout(() => {
                    this.display = true
                }, 1000)
            }
        },

        requestPermission () {
            Notification.requestPermission((status) => {
                if (status === 'denied') {
                    this.display = false

                    this.showMessage(
                        'Li',
                        'Bn  tt quyn thng bo trong trnh duyt.  bt li, hy vo ci t trang web ca trnh duyt v kch hot quyn thng bo.',
                        'error'
                    )
                }
                else if (status === 'granted') {
                    this.display = false
                    this.subscribeNotifi()

                    this.showMessage(
                        'Thnh Cng',
                        'T gi bn s nhn c thng bo v tin nhn v cc hot ng mi.',
                        'success'
                    )
                }
            })
        },

        subscribeNotifi () {
            const bc = new BroadcastChannel('bc1')

            bc.postMessage('grant-notifi-perm')
        },

        showMessage (title, message, type) {
            this.message.title = title
            this.message.message = message
            this.message.type = type
            this.message.isDisplay = !this.message.isDisplay
        }
    }
}
</script>