<template>
<div class="container container-sm">
    <transition name="fade-in">
        <form action="" v-if="!isStartSendEmail" @submit.prevent="sendEmail">
            <h3>Email ca bn:</h3>
            <p class="text-secondary">Mt m xc nhn s c gi ti email ca bn. Nu khng tm thy, vui lng kim tra bao gm hm th spam.</p>

            <label class="input">
                <span class="material-icons">mail</span>
                <input name="email" type="email" required placeholder="Email" v-model="email">
                <p>Email</p>
            </label>

            <button class="btn btn-primary mt-4">
                <span class="material-icons mr-2">send</span>
                Next
            </button>
        </form>
    </transition>

    <transition name="fade-in">
        <form action="" v-if="isSendEmail" @submit.prevent="updatePassword">
            <h3>Nhp m xc nhn</h3>
            <p class="text-secondary">Nhp m xc nhn va c gi n email ca bn. M xc nhn c hiu lc trong 5 pht.</p>

            <label class="input">
                <span class="material-icons">code</span>
                <input name="verificationCode" type="text" required placeholder="Code" v-model="form.verificationCode">
                <p>Code</p>
            </label>
            <br><hr><br>

            <h3>i mt khu</h3>
            <p class="text-secondary">Mt khu phi t 6 k t, bao gm ch in hoa, ch in thng, s v k t c bit.</p>

            <label class="input">
                <span class="material-icons">lock</span>
                <input type="password" required placeholder="New Password" v-model="form.newPassword">
                <p>New Password</p>
            </label>

            <label class="input">
                <span class="material-icons">lock</span>
                <input type="password" required placeholder="Re-Password" v-model="form.rePassword">
                <p>Re-Password</p>
            </label>

            <button class="btn btn-primary mt-4">
                <span class="material-icons mr-2">send</span>
                Next
            </button>
        </form>
    </transition>

    <message-popup
    :title="message.title"
    :message="message.message"
    :type="message.type"
    :is-display="message.isDisplay" />
</div>
</template>

<script>
import api from '@/store/api'
import { mapMutations } from 'vuex'
import MessagePopup from '@/components/MessagePopup.vue'

export default {
    name: 'ForgetPwdView',

    components: {
        MessagePopup
    },

    data () {
        return {
            isStartSendEmail: false,
            isSendEmail: false,
            email: '',
            
            form: {
                verificationCode: '',
                newPassword: '',
                rePassword: ''
            },

            message: {
                title: '',
                message: '',
                type: '',
                isDisplay: false
            }
        }
    },

    methods: {
        ...mapMutations(['setIsLoading']),

        async sendEmail () {
            try {
                this.isStartSendEmail = true
                this.setIsLoading(true)

                let result = await api.post('/forget-password', {
                    email: this.email
                })

                let data = await result.json()

                this.setIsLoading(false)

                if (result.status !== 200) {
                    throw new Error(data.error)
                }

                this.isSendEmail = true
            }
            catch (err) {
                this.isStartSendEmail = false
                this.setIsLoading(false)

                if (err.message == 'Email not found') {
                    this.showMessage(
                        'Li',
                        'Khng tm thy ngi dng',
                        'error'
                    )
                }
                else {
                    this.showMessage(
                        'Li',
                        'Khng th kt ni n my ch',
                        'error'
                    )
                }
            }
        },

        async updatePassword () {
            if (this.form.newPassword !== this.form.rePassword) {
                this.showMessage(
                    'Li',
                    'Mt khu khng khp',
                    'warning'
                )

                throw new Error('Password does not match')
            }

            try {
                this.setIsLoading(true)

                let result = await api.put('/forget-password', {
                    email: this.email,
                    validateCode: this.form.verificationCode,
                    newPassword: this.form.newPassword
                })

                let data = await result.json()

                this.setIsLoading(false)

                if (result.status != 200) {
                    throw new Error(data.error)
                }

                this.showMessage(
                    'Thnh cng!',
                    'Cp nht mt khu mi thnh cng!',
                    'success'
                )

                setTimeout(() => {
                    location.href = '/login'
                }, 3000)
            }
            catch (err) {
                this.setIsLoading(false)

                let msg = ''

                if (err.message == 'Email not found') msg = 'Khng tm thy ngi dng'
                else if (err.message == 'Code has expired') msg = 'M bo mt  ht hn'
                else if (err.message == 'Code is not valid') msg = 'M bo mt khng chnh xc'  
                else if (err.message == 'Update Password failed') msg = 'Cp nht mt khu khng thnh cng, vui lng th li sau'
                else if (err.message == 'Invalid password') msg = 'Mt khu phi t 6 k t, bao gm ch in hoa, ch in thng, s v k t c bit'
                else msg = 'Khng th kt ni n my ch'

                this.showMessage(
                    'Li',
                    msg,
                    'error'
                )
            }
        },

        showMessage (title, message, type) {
            this.message.title = title
            this.message.message = message
            this.message.type = type
            this.message.isDisplay = !this.message.isDisplay
        }
    }
}
</script>